<template>
	<div class="app-viewport" id="file-list">
		<md-sidenav class="md-right md-fixed" ref="sidebar">
			<md-toolbar class="md-account-header">
				<md-list class="md-transparent">
					<md-list-item class="md-avatar-list">
						<md-avatar class="md-large">
							<img src="https://placeimg.com/64/64/people/8" alt="People">
							<!-- <img :src:"{{picture.medium}}" alt="Img"> -->
						</md-avatar>
						<md-list-item>
							<div class="md-list-text-container">
								<span class="md-headline tocap">{{name.first  }} {{name.last }}</span>
								<span></span>
								<span class="email">{{email}}</span>
							</div>

							<md-button class="md-icon-button md-list-action">
								<md-icon>arrow_drop_down</md-icon>
							</md-button>
						</md-list-item>
					</md-list>
				</md-toolbar>
				
				<md-list>
					<md-list-item @click="$refs.sidebar.toggle()" class="md-primary">
						<md-icon>insert_drive_file</md-icon> <span>My files</span>
					</md-list-item>


				</md-list>
			</md-sidenav>	
			<!--                                                     -->
			<md-whiteframe md-elevation="3">
				<md-toolbar class="md-medium">
					<div class="md-toolbar-container">
						
						<div class="spacer">
							<span class="md-display-3 logheading"><span class="loghead2"> Do<span class="loghead3">To</span></span></span></div>


							<span style="flex: 1"></span>

							<md-button class="md-icon-button">
								<md-icon>search</md-icon>
							</md-button>

							<md-button class="md-icon-button" @click="$refs.sidebar.toggle()">
								<md-icon>menu</md-icon>
							</md-button>
						</div>

						<div class="md-toolbar-container">
							<!-- <h2 class="md-title">Your To Do's</h2> -->

							<md-button class="md-fab-bottom-center md-fab">
								<md-icon>add</md-icon>
							</md-button>
						</div>
					</md-toolbar>
				</md-whiteframe>

				<md-list class="md-double-line">
					<!-- <md-subheader class="md-inset">Folders</md-subheader> -->

					<md-list-item v-for="item in sort">
						<md-avatar class="md-avatar-icon">
							<md-icon class="">
								{{item.icon}}
							</md-icon>
						</md-avatar>
						<div class="md-list-text-container">
							<span class="tocap">{{item.description }}</span>
							<p>{{item.time | moment }} {{item.time | relative }} </p>
						</div>
						<md-button class="md-icon-button md-list-action">
							<md-icon style="color:#AED581">update</md-icon>
						</md-button>
						<md-button class="md-icon-button md-list-action">
							<md-icon style="color:#EF5350">delete</md-icon>
						</md-button>
						<md-button class="md-icon-button md-list-action">
							<md-icon style="color:#64B5F6">info</md-icon>
						</md-button>
					</md-list-item>
					<!-- 	 -->
					<md-list-item>
						<md-avatar class="md-avatar-icon">
							<md-icon>folder</md-icon>
						</md-avatar>

						<div class="md-list-text-container">
							<span>Photos</span>
							<p>Jan 9, 2014</p>
						</div>

						<md-button class="md-icon-button md-list-action">
							<md-icon>info</md-icon>
						</md-button>
					</md-list-item>

					<!-- <md-subheader class="md-inset">Files</md-subheader> -->
				</md-list>
			</div>
		</template>

		<script>
			import moment from "moment"
			export default {
				data(){
					return{
						name:'',
						items:null,
						email:'',
						picture:''
					}
				},
				created:function(){
					
					console.log("main",this.$route.params)
					this.name = this.$route.params.name
					this.items = this.$route.params.items
					this.email = this.$route.params.email
					this.picture = this.$route.params.picture
				},
				computed:{
					sort(item){
						return this.items.sort(function(a,b){
							return a.time < b.time;
						})
					}
				},
				filters: {
					moment:function (date) {
						return moment(date).format('dddd Do');
					},
					relative:function(date){
						return moment(date).fromNow();        
				     }
				}
			}
		</script>

		<style scoped>
			.md-button.md-fab.md-fab-bottom-center{
				bottom:-25px;
				left:45%;
				transform:scale(1.5);
			}
			.spacer{
				margin-left:1.5%;
				margin-top:70px;
				color:white;
			}
			.loghead2{
				padding:4px;
				background: #f44336;
				text-shadow: 2px 8px 6px rgba(0,0,0,0.2),
				0px -5px 35px rgba(255,255,255,0.3);
				border-radius:5%;
			}
			.loghead3{
				color: rgba(255, 255, 255, .87);

			}
			.email:hover{
				color:white;
			}
			.tocap{
				text-transform: capitalize;
			}
		</style>


